<template>
  <div class="home">
    <h1 class="title"><img src="@/assets/title.png" alt=""></h1>
    <p class="about">コンセプト評価まで残った練習生をポチポチ選んでね。色分けはコンセプト評価のレッスン着の色だよ<br>画像作成ボタンを押すと保存できる画像を作成するよ</p>
    <div class="result-wrap">
      <p class="result-text">↓画像を用意したよ↓</p>
      <div id="result"></div>
      <p class="result-text">リセットボタンを押すと消えるよ</p>
    </div>
    <div id="capture">
      <div class="logo"><img src="@/assets/logo-pudu.png" alt="" class=""></div>
      <ol class="member-list">
        <li class="member-list_item" v-for="(item, index) in checkedNames" v-bind:key="index">
          <div v-if="item === ''" class="member"><img src="@/assets/icon-null.png" alt="" class="member_img"></div>
          <div v-else v-on:click="m(index)" class="member is-set"><img v-bind:src="require('@/assets/icon-' + item + '.png')" alt="" class="member_img"></div>
        </li>
      </ol>
    </div>
    <div class="btn-wrap">
      <button v-on:click="allclear" class="btn is-clear">リセット</button>
      <button v-on:click="capture" class="btn is-capture">画像作成</button>
    </div>
    <ul class="trainee-list">
      <li class="trainee-list_item trainee" v-for="(item, index) in list" v-bind:item="item.name" v-bind:key="item.id">
        <input class="trainee_ck" type="checkbox" v-bind:id="item.name" v-bind:value="item.name" v-bind:disabled="item.disabled" v-bind:checked="item.view" true-value="yes" false-value="no" v-on:change="c(index, item.id)">
        <label v-bind:for="item.name" class="trainee_label">
          <img v-bind:src="require('@/assets/icon-' + item.id + '.png')" alt="" class="trainee_img">
          <p class="trainee_name">{{ item.name }}</p>
        </label>
      </li>
    </ul>
  </div>
</template>
<script src="@/assets/lib/html2canvas.js"></script>

<script>
export default {
  metaInfo: {
    title: 'コンセプト評価',
    titleTemplate: '%s | 最強デビューメンバーを結成せよ',
    htmlAttrs: {
      lang: 'ja',
      amp: true
    },
    meta: [
      { charset: 'utf-8' },
      {
        property: 'og:site_name',
        content: '最強デビューメンバーを結成せよ'
      },
      {
        property: 'og:type',
        content: 'website'
      },
      {
        'property': 'og:title',
        'content': 'コンセプト評価',
        'template': '%s | 最強デビューメンバーを結成せよ',
        'vmid': 'og:title'
      },
      {
        property: 'og:url',
        content: 'https://onodo.github.io'
      },
      {
        property: 'og:description',
        content: 'デビューしよう！'
      },
      {
        property: 'og:image',
        content: 'https://onodo.github.io/img/ogp.jpg'
      },
      {
        name: 'twitter:card',
        content: 'summary_large_image'
      }
    ]
  },
  data () {
    return {
      list: [
        { id: '01', name: '김우석', view: false, disabled: false },
        { id: '02', name: '이진혁', view: false, disabled: false },
        { id: '03', name: '김요한', view: false, disabled: false },
        { id: '04', name: '송형준', view: false, disabled: false },
        { id: '05', name: '구정모', view: false, disabled: false },
        { id: '06', name: '이은상', view: false, disabled: false },
        { id: '07', name: '남도현', view: false, disabled: false },
        { id: '08', name: '이진우', view: false, disabled: false },
        { id: '09', name: '한승우', view: false, disabled: false },
        { id: '10', name: '김민규', view: false, disabled: false },
        { id: '11', name: '손동표', view: false, disabled: false },
        { id: '12', name: '송유빈', view: false, disabled: false },
        { id: '13', name: '차준호', view: false, disabled: false },
        { id: '14', name: '함원진', view: false, disabled: false },
        { id: '15', name: '이한결', view: false, disabled: false },
        { id: '16', name: '최병찬', view: false, disabled: false },
        { id: '17', name: '조승연', view: false, disabled: false },
        { id: '18', name: '황윤성', view: false, disabled: false },
        { id: '19', name: '금동현', view: false, disabled: false },
        { id: '20', name: '강현수', view: false, disabled: false },
        { id: '21', name: '이세진', view: false, disabled: false },
        { id: '22', name: '김국헌', view: false, disabled: false },
        { id: '23', name: '강민희', view: false, disabled: false },
        { id: '24', name: '김시훈', view: false, disabled: false },
        { id: '25', name: '김현빈', view: false, disabled: false },
        { id: '26', name: '이협', view: false, disabled: false },
        { id: '27', name: '토니', view: false, disabled: false },
        { id: '28', name: '박선호', view: false, disabled: false },
        { id: '29', name: '최수환', view: false, disabled: false },
        { id: '30', name: '주창욱', view: false, disabled: false },
        { id: '31', name: '김동윤', view: false, disabled: false }
      ],
      checkedNames: ['', '', '', '', '', '', '', '', '', '', '']
    }
  },
  methods: {
    c (i, id) {
      let ck = this.list[i].view
      this.list[i].view = !ck
      if (!ck) {
        let index = this.checkedNames.indexOf('')
        this.checkedNames[index] = id
      } else {
        let index = this.checkedNames.indexOf(id)
        this.checkedNames[index] = ''
      }
      if (this.checkedNames.indexOf('') === -1) {
        for (let e in this.list) {
          if (this.list[e].view === false) {
            this.list[e].disabled = true
          }
        }
      } else if (this.checkedNames.indexOf('') !== -1) {
        for (let e in this.list) {
          this.list[e].disabled = false
        }
      }
    },
    m (i) {
      let src = this.checkedNames[i]
      let index = this.list.findIndex(({ id }) => id === src)
      this.list[index].view = false
      this.checkedNames[i] = ''
      if (this.checkedNames.indexOf('') !== -1) {
        for (let e in this.list) {
          this.list[e].disabled = false
        }
      }
    },
    allclear () {
      const wrap = document.querySelector('.result-wrap')
      const re = document.querySelector("#result")
      if (re.childNodes.length !== 0) {
        re.textContent = null
        wrap.classList.remove('is-show')
      }
      for (let e in this.list) {
        this.list[e].disabled = false
        this.list[e].view = false
      }
      for (let i in this.checkedNames) {
        this.checkedNames[i] = ''
      }
    },
    capture () {
      window.scrollTo(0, 0)
      const wrap = document.querySelector('.result-wrap')
      const img = document.createElement('img')
      wrap.classList.add('is-show')
      img.className = 'result_img'
      img.style.cssText = 'max-width: 100%'
      html2canvas(document.querySelector("#capture")).then(canvas => {
        const re = document.querySelector("#result")
        const src = canvas.toDataURL()
        img.setAttribute('src', src)
        if (re.childNodes.length !== 0) {
          re.textContent = null
          re.appendChild(img)
        } else {
          re.appendChild(img)
        }
      })
    }
  }
}
</script>
